body {
  margin: 0;
  background-image: linear-gradient(to right, #F5E8CB, #96DEDA);
}

.main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.title {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.flex {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.fluid {
  width: 100%;
}

.content {
  margin-top: 56px;
}

.column {
  width: 100%;
}

.reset-button {
  width: 100%;
  max-width: 80px;
}

.button {
  height: 56px;
}

.item-field {
  width: calc((100% - 8px)/2);
}

.item-field:nth-child(2n+1) {
  margin-right: 8px;
}

.item-field:nth-child(n+3) {
  margin-top: 8px;
}

.filters {
  box-sizing: border-box;
  width: 100%;
  padding: 0 16px 0 16px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.filter-select {
  width: calc((100% - 8px)/2);
  margin-right: 8px;
}

.filter-select:nth-child(2n+2) {
  margin-right: 0;
}

.filter-select:nth-child(n+3) {
  margin-top: 8px
}

/* width < 600px */

@media screen and (min-width:600px) {
  .content {
    margin-top: 64px;
  }
}

/* width >= 960px */

@media screen and (min-width: 960px) {
  .filters {
    padding: 0 16px 0 0;
  }
  .column {
    width: calc((100% - 8px * 2)/3);
    margin-right: 8px;
  }
  .column:last-child {
    margin-right: 0;
  }
}

@media screen and (min-width:1280px) {
  .filter-select {
    width: calc((100% - 8px * 3)/4);
  }
  .filter-select:nth-child(2) {
    margin-right: 8px;
  }
  .filter-select:last-child {
    margin-right: 0;
  }
  .filter-select:nth-child(n+3) {
    margin-top: 0;
  }
}

